import React from 'react';
import { useQuery } from 'react-query';
import { useParams } from 'react-router';
import { get, getUrl } from './service';
import { useTheme } from './theme';
import { Pokemon as PokemonResponse } from './model/ResponseTypes';
import { Theme } from './theme/types';
import { StatusContainer } from './utility';

type ParamsType = {
  id: string;
};

const makeStyles = ({ palette: colors, spacing }: Theme) => {
  // TODO: figure out best way to do mobile styles
  const cardSize = 256;
  // TODO: put these in theme
  const cardBackground = '#555';
  const cardBorderColor = '#222';
  const nameTitleTopBorder = '#aaa';
  const nameTitleBackground = '#888';

  return {
    detailCard: {
      alignItems: 'flex-end',
      backgroundColor: cardBackground,
      backgroundSize: '100%',
      backgroundPositionX: 'center',
      backgroundRepeat: 'no-repeat',
      border: `2px solid ${cardBorderColor}`,
      display: 'flex',
      imageRendering: 'pixelated',
      justifyContent: 'center',
      height: cardSize,
      lineBreak: 'normal',
      margin: '0 auto',
      width: cardSize,
    },
    name: {
      backgroundColor: nameTitleBackground,
      borderTop: `2px solid ${nameTitleTopBorder}`,
      padding: spacing,
      width: '100%',
    },
  };
};

export default function DetailCard() {
  const styles = makeStyles(useTheme().state);
  const { id } = useParams<ParamsType>();
  const resourceUrl = getUrl(`pokemon/${id}`);
  const { isLoading, error, data } = useQuery<PokemonResponse, Error>(
    resourceUrl,
    () => get(resourceUrl),
  );
  const statusProps = { isLoading, error };

  const pokemon: PokemonResponse | undefined = data;
  const pokemonName = pokemon
    ? pokemon.name.replace(/^\w/, (l) => l.toUpperCase())
    : '';

  return (
    <div>
      <section
        // @ts-ignore
        style={{
          ...styles.detailCard,
          backgroundImage: `url(${data?.sprites.front_default})`,
        }}
      >
        <StatusContainer {...statusProps}>
          <article style={styles.name}>{pokemonName}</article>
        </StatusContainer>
      </section>
    </div>
  );
}
