import React, { useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { QueryClient, QueryClientProvider } from 'react-query';
import { ThemeProvider, ThemeContext } from './theme';
import { Theme } from './theme/types';
import Dashboard from './Dashboard';
import DetailCard from './DetailCard';
import { getLogo } from './service';

const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      refetchOnWindowFocus: false,
    },
  },
});

const makeStyles = ({ palette: colors, spacing }: Theme, logo: string) => {
  const { innerWidth } = window;
  const headerFontSize = innerWidth <= 480 ? '3rem' : '5rem';
  const logoSize = innerWidth <= 480 ? 60 : 90;

  return {
    app: {
      backgroundColor: colors.background.default,
      color: colors.text.primary,
      minHeight: '100%',
      overflow: 'scroll',
    },
    header: {
      alignItems: 'center',
      display: 'flex',
      fontSize: headerFontSize,
      fontWeight: 300,
      justifyContent: 'center',
      marginBottom: '2rem',
      marginLeft: spacing,
      marginRight: spacing,
    },
    // <img src={logo} style={styles.logo} alt="Logo" />
    logo: {
      backgroundImage: `url(${logo})`,
      backgroundPosition: 'center',
      backgroundRepeat: 'no-repeat',
      backgroundSize: '150%',
      height: logoSize,
      imageRendering: 'pixelated',
      width: logoSize,
    },
    table: { margin: 32, width: '90%' },
  };
};

export default function App() {
  const [logo, setLogo] = React.useState('');
  useEffect(() => {
    queryClient
      .fetchQuery<any, Error, any>('pokeBall', getLogo)
      .then((item) => {
        setLogo(item.sprites.default);
      });
  }, []);

  return (
    <ThemeProvider>
      <ThemeContext.Consumer>
        {({ dispatch, state: theme }) => {
          const styles = makeStyles(theme, logo);

          return (
            <QueryClientProvider client={queryClient}>
              <main style={styles.app}>
                <h1 style={styles.header}>
                  {/* @ts-ignore */}
                  POKÃ‰<span style={styles.logo}></span>DEX
                </h1>
                <Router>
                  <Switch>
                    <Route path="/" exact>
                      <Dashboard />
                    </Route>
                    <Route path="/:id">
                      <DetailCard />
                    </Route>
                  </Switch>
                </Router>
              </main>
            </QueryClientProvider>
          );
        }}
      </ThemeContext.Consumer>
    </ThemeProvider>
  );
}
