const defaultTheme = { ...dark, spacing: 8, text, logo };
const ThemeContext = React.createContext<ThemeProviderType>({
  dispatch: () => {},
  state: defaultTheme,
});

function themeReducer(prevState: Theme, themeName: string): Theme {
  switch (themeName) {
    case 'light':
      return { ...light, logo, spacing: 8, text };
    default:
      return defaultTheme;
  }
}

function ThemeProvider({ children }: ProviderProps) {
  const [state, dispatch] = React.useReducer(themeReducer, defaultTheme);
  const value = { dispatch, state };

  return (
    <ThemeContext.Provider value={value}>{children}</ThemeContext.Provider>
  );
}

function useTheme() {
  const ctx = React.useContext(ThemeContext);
  if (!ctx) {
    throw new Error('useAccess must be used within a AccessProvider');
  }
  return ctx;
}

export { ThemeContext, ThemeProvider, useTheme };
