import React from 'react';
import { useInfiniteQuery, useQuery } from 'react-query';
import { parse } from 'querystring';
import { useTheme } from './theme';
import { Theme } from './theme/types';
import { getLogo, getAll } from './service';
import { PokemonList, PokemonResource } from './model/ResponseTypes';
import GridCard from './GridCard';
import { BottomSpacer, Error, Loading } from './utility';

const { innerWidth } = window;
const IS_MOBILE = innerWidth <= 480;

const makeStyles = ({ palette: colors, spacing }: Theme) => {
  // TODO: figure out best way to do mobile styles
  const margin = IS_MOBILE ? 0 : spacing * 2;
  const spinnerSize = IS_MOBILE ? 45 : 60;

  return {
    grid: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'center',
      margin,
    },
    spinner: {
      animationDuration: '1s',
      animationIterationCount: 'infinite',
      animationName: 'rotateBall',
      animationDirection: 'forwards',
      transformOrigin: 'center center',
      height: spinnerSize,
      imageRendering: 'pixelated',
      margin: spacing * 2,
      marginBottom: spacing * 3,
      width: spinnerSize,
    },
  };
};

function onScrollEnd(callback: Function, hasNextPage?: boolean) {
  const html = document.querySelector('html');
  if (
    html &&
    html.scrollTop >= html.scrollHeight - window.innerHeight &&
    hasNextPage
  ) {
    callback();
  }
}

export default function Dashboard() {
  const styles = makeStyles(useTheme().state);
  const { isLoading: isLoadingBall, data: spinner } = useQuery(
    'pokeBall',
    getLogo,
  );
  const {
    data,
    error,
    fetchNextPage,
    hasNextPage,
    isFetchingNextPage,
    status,
  } = useInfiniteQuery<PokemonList, Error>('getAll', getAll, {
    getNextPageParam: (lastPage, pages) => {
      if (lastPage.next) {
        const queryString = parse(new URL(lastPage.next).search.slice(1));
        return queryString.offset;
      }
    },
  });
  const scrollListener = React.useCallback(() => {
    document.addEventListener('scroll', () =>
      onScrollEnd(fetchNextPage, hasNextPage),
    );
  }, [fetchNextPage, hasNextPage]);

  React.useEffect(() => {
    scrollListener();
  }, [scrollListener]);

  if (status === 'loading') {
    return <Loading />;
  } else if (status === 'error') {
    return <Error error={error} />;
  }

  const { pages } = data!;
  return (
    <>
      {/* @ts-ignore */}
      <div style={styles.grid}>
        {pages.map((page) => {
          return page.results.map((resource: PokemonResource) => (
            <GridCard key={resource.name} resource={resource}></GridCard>
          ));
        })}
      </div>
      {!isLoadingBall && isFetchingNextPage ? (
        <img
          src={spinner!.sprites.default}
          alt="Loading"
          // @ts-ignore
          style={styles.spinner}
        />
      ) : (
        <BottomSpacer />
      )}
    </>
  );
}
