import React, { ReactElement } from 'react';

export const BottomSpacer = () => (
  <div style={{ marginTop: 32, marginBottom: 64 }}></div>
);

export const Error = ({ error }: { error: Error | null }) => (
  <div>Error: {error?.message}</div>
);

export const Loading = () => <div>Loading...</div>;

type StatusProps = {
  children: ReactElement;
  error: Error | null;
  isLoading: boolean;
};
export const StatusContainer = ({
  children,
  error,
  isLoading,
}: StatusProps) => {
  if (error) {
    return <Error error={error} />;
  } else if (isLoading) {
    return <Loading />;
  }

  return children;
};
